import React, { useState } from 'react';

import { Card, Form, Button } from "react-bootstrap";
import DatePicker from "react-date-picker";
import DisplayData from './DisplayData';
import ApiCalendar from 'react-google-calendar-api';

const CustomTimePeriod = () => {
    
    const [startDate, setStartDate] = useState(false);
    const [endDate, setEndDate] = useState(false);
    const [processed, setProcessed] = useState(false);
    const [data, setData] = useState([]);
    const [dateRangeString, setDateRangeString] = useState('temp')

    const fetchEvents = async () => {
        try {
            const tempStartDate = new Date(startDate);
            const tempEndDate = new Date(endDate);

            setDateRangeString(`${startDate} - ${endDate}`);

            const { result } = await ApiCalendar.listEvents({
                timeMin: tempStartDate.toISOString(),
                timeMax: tempEndDate.toISOString(),
                showDeleted: false,
                maxResults: 10000,
                orderBy: 'startTime',
                singleEvents: true
            });

            result['success'] = true
            return result;
        } catch (error) {
            console.log(error);
            return { 'success': false };
        }
    }

    const processDates = async () => {
        if (startDate && endDate) {
            const res = await fetchEvents();
            if (res.success) {
                setData(res.items);
                setProcessed(true);
            }
        }
    }
    
    return (
        <Card style={{"marginBottom": "10px"}}>
            <Card.Header>Custom Time Period</Card.Header>
            <Card.Body>
                <Form.Group>
                    <Form.Label>Start Date</Form.Label>
                    <Form.Control type="date" onChange={(date) => setStartDate(date.target.value)}></Form.Control>
                    <Form.Label>End Date</Form.Label>
                    <Form.Control type="date" onChange={(date) => setEndDate(date.target.value)}></Form.Control>
                    <Button onClick={() => processDates()} style={{"marginTop": "8px"}}>Process</Button>
                </Form.Group>
            </Card.Body>
            {processed ? <DisplayData data={data} dateRangeString={dateRangeString} style={{"marginTop": "20px"}}/> : <></>}
        </Card>
    )
    
}

export default CustomTimePeriod;